@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar {
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;
    
    &.mostrar {
        padding: 3rem;
        opacity: 1;
        height: auto;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgb( 0 0 0 / .90);

        @include m.tablet {
            display: none;
        }
    }

    &.ocultar {
        opacity: 0;
    }
    
    @include m.tablet {
        padding: 3rem;
        display: unset;
        opacity: 1;
        height: auto;
    }

    .contenedor_sidebar {
        display: flex;
        justify-content: space-between;
        align-items: center;

        h2 {
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }

        .cerrar_menu {
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;

            img {
                &:hover {
                    cursor: pointer;
                    background-color: lighten(v.$negro, 10%);
                }
            }

            @include m.tablet {
                display: none;
            };
        }

    }


    .sidebar_nav {
        margin-top: 5rem;

        a {
            color: v.$blanco;
            display: block;
            padding: .7rem;
            border-radius: .5rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            font-weight: 700;
            margin-bottom: 2rem;
            text-align: center;
            
            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 20%);
            }
            
            @include m.tablet {
                margin-bottom: 1.5rem;
                font-size: 1.2rem;
                text-align: left;
            }
        }
    }

    .cerrar_sesion_mobile {
        margin-top: 8rem;

        .cerrar_sesion {
            @include m.boton(v.$indigo, v.$blanco);
        }

        @include m.tablet {
            display: none;
        };
    }
}