@use 'base/variables' as v;
@use 'base/mixins' as m;

.filtros{
    background-color: v.$blanco;
    margin-top: 3rem;
    border-radius: 1rem;
    padding: 2rem;

    h2 {
        color: v.$indigo;
        font-size: 2.5rem;

        @include m.tablet {
            margin: 0;
        };
            
    }

    .filtros_inputs {
        @include m.tablet {
            display: flex;
            align-items: center;
            gap: 3rem;
        };
    }

    .campo {
        margin-bottom: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;

        @include m.tablet {
            margin-bottom: 0;
        };

        input[type="radio"] {
            accent-color: v.$indigo;
            width: 1.6rem;
            height: 1.6rem;
        }
    }
}