@use 'base/mixins' as m;
@use 'base/variables' as v;

.barra_mobil {
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include m.tablet {
        display: none;
    };

    h1 {
        color: v.$blanco;
        margin: 0;
    }

    .menu {
        padding: .5rem 2rem;
        border-radius: 50%;

        img {
            width: .5rem;

        }
        &:hover {
            cursor: pointer;
        }
    }
}

.barra {
    display: none;

    @include m.tablet {
        background-color: v.$blanco;
        padding: 1rem 2rem;
        box-shadow: .1rem 1.2rem 0.9rem -1.3rem rgba( 0, 0, 0, .4);
    
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    
        p {
            margin: 0;
            font-size: 1.4rem;
    
            span {
                font-weight: 700;
                color: v.$indigo;
            }
        }
    
        .cerrar_sesion {
            @include m.boton( darken(v.$indigo, 20%), v.$blanco );
            font-size: 1.4rem;
            padding: .8rem;
            border-radius: .5rem;
        }
    };
}