@use 'base/mixins' as m;
@use 'base/variables' as v;

@media (prefers-color-scheme: dark) {
    $grisOscuro: #2C2C3B;
    body {
        background-color: $grisOscuro;
    }

    .tagline,
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a {
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input,
    .nombre_pagina {
        color: darken(v.$grisClaro, 15%);
    }

    .formulario .campo input,
    .dashboard .formulario input:not([type=submit]) {
        background-color: lighten($grisOscuro, 20%);
        border: none;
        color: v.$blanco;

        &::placeholder {
            color: darken(v.$grisClaro, 25%);
        }
    }

    .alerta.error,
    .alerta.exito {
        background-color: v.$blanco;
    }

    .sidebar {
        @include m.tablet {
            background-color: darken($grisOscuro, 5%);
        }
        
        .sidebar_nav a {
            
            &.activo,
            &:hover {
                background-color: darken(v.$naranja, 5%);
            }
        }
    }
    
    .barra,
    .barra_mobil {
        background-color: darken($grisOscuro, 7%);
    }

    .barra {
        
        p {
        color: v.$grisClaro;

            span {
                color: v.$naranja;
            }
        }

        .cerrar_sesion {
            @include m.boton(v.$naranja, v.$blanco);
            padding: .8rem;
            font-size: 1.4rem;
        }
    }

    .filtros {
        background-color: lighten($grisOscuro, 10%);

        h2,
        label {
            color: v.$blanco;
        }

        .campo input[type="radio"] {
            accent-color: v.$naranja;
        }
    }

    .dashboard {
        background-color: $grisOscuro;

        .enlace {
            color: v.$grisClaro;

            &:hover {
                color: darken(v.$grisClaro, 20%);
            }
        }
    }

    .listado_proyectos .proyecto {
        background-color: lighten($grisOscuro, 20%);
        
        &:hover {
            background-color: lighten($grisOscuro, 15%);
        }
    }

    .listado_tareas .tarea {
        background-color: lighten($grisOscuro, 10%);
        border: none;

        p {
            color: v.$grisClaro;
        }
    }

    .modal .formulario {
        background-color: $grisOscuro;
         
        legend {
            color: v.$grisClaro;
        }
    }
}